}

button:hover {
  color: #000;
  transition-duration: .5s;
}

.search-wrapper input {
  width: 60%;
  color: #505168;
  background: none;
  border: none;
  border-bottom: 2px solid #505168cc;
  outline: none;
  margin-top: 1em;
  font-size: 1em;
}

h3, h2 {
